import './dip.less'
import Button from '../../components/Button/Button'
export default function DipOne({ lang }) {
  return (
    <div className='dip-box'>
      <div className='dip-box-info'>
        <div className='dip-info'>
          <div className='dip-info-title'>
            <div className='dip-title'>
              {lang['dip-one']}
            </div>
            <div className='dip-des'>
              {lang['governance-title']}
            </div>
          </div>
          <div className='dip-options'>
            <div className='dip-options-checked'>
              <div className='checkbox'>
                <input
                  type='radio'
                  name='option'
                  value='1'
                  id="I"
                />
                <label for='I'>I.</label>
              </div>
              <div className='option-describe'>
                <div>
                  <div>
                    {lang['governance-options-one']}
                  </div>
                  <div>
                    {lang['governance-options-one-describe']}
                  </div>
                </div>

              </div>
              <div className='peng'>
                <div className='peng-box'>
                  <div className='peng-bg dip-one-one'></div>
                  <div className='peng-num'>
                    158,544 DERI
                  </div>
                </div>
              </div>
            </div>
            <div className='dip-options-checked'>
              <div className='checkbox'>
                <input
                  type='radio'
                  name='option'
                  value='2'
                  id="II"
                />
                <label for='II'>II.</label>
              </div>
              <div className='option-describe'>
                <div>
                  <div>
                    {lang['governance-options-two']}
                  </div>
                  <div>
                    {lang['governance-options-two-describe']}
                  </div>
                </div>

              </div>
              <div className='peng'>
                <div className='peng-box'>
                  <div className='peng-bg dip-one-two'></div>
                  <div className='peng-num'>
                    334,253 DERI
                  </div>
                </div>
              </div>
            </div>
            <div className='dip-options-checked'>
              <div className='checkbox'>
                <input
                  type='radio'
                  name='option'
                  value='3'
                  id="III"
                />
                <label for='III'>III.</label>
              </div>
              <div className='option-describe'>
                <div>
                  <div>
                    {lang['governance-options-three']}
                  </div>
                </div>

              </div>
              <div className='peng'>
                <div className='peng-box'>
                  <div className='peng-bg dip-one-three'></div>
                  <div className='peng-num'>
                    0 DERI
                  </div>
                </div>
              </div>
            </div>
            <div className='vote-button'>
              <Button label={lang['vote']} width='332' fontSize='18' />
            </div>
          </div>

        </div>
        <div className='voting-rules'>
          <div className='voting-rules-title'>
            {lang['voting-rules']} :
            </div>
          <div className='voting-info'>
            <div>1. {lang['vote-rules-one']}</div>
            <div>{lang['vote-rules-one-describe']}</div>
          </div>
          <div className='voting-info'>
            <div>2.{lang['vote-rules-two']}</div>
          </div>
          <div className='voting-info'>
            <div>3.{lang['vote-rules-three']}</div>
          </div>
          <div className='voting-info'>
            <div>4.{lang['vote-rules-four']}</div>
          </div>
        </div>
      </div>
    </div>
  )
}


